import React from 'react';
import { Row, Col, Card, Form, ListGroup, Button, Table, Dropdown } from 'react-bootstrap';
import config from '../../config';
import Aux from "../../hoc/_Aux";
import UcFirst from "../../App/components/UcFirst";
import MainCard from "../../App/components/MainCard";

class jobPostStatus extends React.Component {
    constructor(props) {
        super(props);
        this.state = { templates: [], selectedTemplate: {}, reports: [], loading: true, error: false, errorMessage: null, report: false, reportLoad: false };
    }

    componentDidMount() {
        let apiUrl = config.baseurl + 'jobs';
        fetch(apiUrl)
            .then((response) => response.json())
            .then((data) => this.setState({ templates: data, loading: false }));
    }

    handleJobChange = (e) => {

        let jid = e.target.value;
        if (jid !== 'null') {
            let templateData = this.state.templates.find(e => e.jid == jid);
            this.setState({ selectedTemplate: templateData })
        } else {
            this.setState({ selectedTemplate: {} })
        }

    };

    showJobPostReport = () => {
        let selectedTemplate = this.state.selectedTemplate;
        if (typeof selectedTemplate.jid == 'undefined') {
            this.setState({ error: true, errorMessage: 'Please Select Job Template' });
        } else {
            this.setState({ reportLoad: true, error: false, errorMessage: '' });

            let selectedTemplate = this.state.selectedTemplate;

            let apiUrl = config.baseurl + 'jobs/' + selectedTemplate.jid + '/job_status';

            let requestOptions = {
                method: 'GET',
                headers: { "Accept": "application/json", "Content-Type": "application/json" },
            };

            fetch(apiUrl, requestOptions)
                .then((response) => response.json())
                .then((data) => this.setState({ report: true, reports: data, reportLoad: false }));
        }
    }

    render() {
        let templates = this.state.templates;
        let selectedTemplate = this.state.selectedTemplate;
        let reports = this.state.reports;

        if (typeof selectedTemplate.jid == 'undefined') {
            selectedTemplate = null;
        }

        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">Job Template</Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <h5>Job Post</h5>
                                <hr />
                                <Row>
                                    <Col md={12}>
                                        <Form.Group>
                                            <Form.Control key={'jobTemplate'} id="jobTemplate" as="select" className="mb-3" onChange={this.handleJobChange}>
                                                <option key={'Select Job Template'} value={'null'}>{'Select Job Template'}</option>
                                                {
                                                    templates.map((template, index) => (
                                                        <option key={index} value={template.jid}>{template.title}</option>
                                                    ))
                                                }
                                            </Form.Control>
                                            {
                                                this.state.error ? (
                                                    <Form.Text className="text-danger">{this.state.errorMessage}</Form.Text>
                                                ) : null
                                            }
                                        </Form.Group>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col md={12}>
                                        <Button onClick={this.showJobPostReport} variant={'primary'}><UcFirst text={'Show Post Report'} /></Button>
                                    </Col>
                                </Row>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
                {
                    this.state.report ? (
                        <Row>
                            <Col>
                                <MainCard title='Job Post Report' loading={this.state.reportLoad} loadingFunction={this.showJobPostReport} isOption>
                                    <Table responsive>
                                        <thead>
                                            <tr>
                                                <th>Name</th>
                                                <th>User Name</th>
                                                <th>Message</th>
                                                <th>Status</th>
                                            </tr>
                                        </thead>
                                        <tbody>

                                            {
                                                reports.map((report, index) => (
                                                    <tr>
                                                        <td>{report.name}</td>
                                                        <td>{report.username}</td>
                                                        <td style={{ width: 200 }}>{report.error_messages}</td>
                                                        <td>{report.status}</td>
                                                    </tr>
                                                ))
                                            }

                                        </tbody>
                                    </Table>
                                </MainCard>
                            </Col>
                        </Row>
                    ) : null
                }
            </Aux>
        );
    }
}

export default jobPostStatus;